//     URL    
export const getBaseUrl = () => {
  //  development    ( )
  if (process.env.NODE_ENV === 'development') {
    return '';
  }
  
  //  production     IP  
  return process.env.REACT_APP_API_BASE_URL || 'http://10.77.98.1:8000';
}; 